.footer{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap : 50px;
}
.footer-logo{
    display : flex;
    align-items: center;
    gap : 20px;
}
.footer-logo p{
    color : #383838;
    font-size : 46px;
    font-weight : 700;
}
.footer-links{
    display : flex;
    list-style : none;
    gap : 50px;
    color : #252525;
    font-size : 20px;
}
.footer-social-icon{
    display : flex;
    gap : 20px;
}
.footer-icons-container{
    padding : 10px;
    padding-bottom: 6px;
    background : #fbfbfb;
    border : 1px solid #ebebeb;
    cursor : pointer;
}
.footer-copyright{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap : 30px;
    width : 100%;
    color : #1a1a1a;
    font-size : 20px;
}
.footer-copyright hr{
    width : 90%;
    border : none;
    height : 3px;
    background: #c7c7c7;
}
@media(max-width:1280px){
    .footer{
        gap : 20px;
    }
    .footer-logo{
        gap : 10px;
        align-items: end;
    }
    .footer-logo img{
        width : 40px;
    }
    .footer-logo p{
        font-size : 25px;
    }
    .footer-links{
        font-size : 14px;
        gap : 10px;
    }
    .footer-social-icon img{
        width : 22px;
    }
    .footer-copyright{
        font-size : 13px;
    }
}